.header {
    background: #141E30;
    background: -webkit-linear-gradient(to top, #243B55, #141E30);
    background: linear-gradient(to top, #243B55, #141E30);
    
}

.home-grid {
    background: #CDCDCD;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


.landing-grid {
    background: #CDCDCD;  /* fallback for old browsers */
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;

}

.clue-body {
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
    background: #CDCDCD;
  }
  
  .clue-grid {
    text-align: center;
    justify-content: center;
  }
  
  .clue-grid h2 {
    font-family: 'RuneScape UF', sans-serif;
  }
  
  .clue-grid p {
    font-family: 'RuneScape UF', sans-serif;
  }
  
  .clue-grid hr {
    border-top: 5px solid black;
    width: 50%;
    margin: auto;
  }
  
  /* .clue-list i {
    font-size: 50px;
    padding-right: 5rem;
  } */

.loot-table {
    max-width: 100%;
    padding: 10%;
}

@font-face {
    font-family: 'RuneScape UF';
    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
        url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
}